@import url('https://fonts.googleapis.com/css?family=Crete+Round:400i');

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100%;
  height: 100%;
  background-image: -webkit-gradient( linear , left top, left bottom , from(rgb(204, 204, 255)) , color-stop(rgb(229, 204, 255)), to(rgb(255, 204, 229)));
  background-image: -webkit-linear-gradient( rgb(204, 204, 255) , rgb(229, 204, 255), rgb(255, 204, 229));
  background-image: -o-linear-gradient( rgb(204, 204, 255) , rgb(229, 204, 255), rgb(255, 204, 229));
  background-image: linear-gradient( rgb(204, 204, 255) , rgb(229, 204, 255), rgb(255, 204, 229));
  background-repeat: no-repeat;
  background-attachment: fixed;
}

h1{
  font-family: 'Crete Round', serif;
}

input {
  font-size: 20px;
  border-radius: 10px;
  outline: none;
}

.submit {
  border: 2px solid black;
  outline: none;
  margin-left: 10px;
  font-size: 20px;
  border-radius: 10px;
  cursor: pointer;
}

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 600px;
}

.todo-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 50%;
}
.input-container{
  height: 50%;
}
.added-item-container {
  margin-left: 10px;
  margin-top: 10px;
}



